Office.onReady(function () {
  console.log("office loaded successfuly");
});
document.addEventListener("DOMContentLoaded", () => {
  const form = document.getElementById("ticketForm");
  const serviceLevel1 = document.getElementById("serviceLevel1");
  const serviceLevel2 = document.getElementById("serviceLevel2");
  const issueDescription = document.getElementById("issueDescription");
  const submitButton = document.getElementById("submitButton");

  let selectedData = {};

  const data = {
    ServiceLevel1: [
      {
        category: "Software",
        subcategories: [
        "Operating system issues (Windows, MacOS, Linux..)",
        "Application software issues (Microsoft Office, Adobe Creative Suite..)",
        "Email/Communication SW issues (Outlook, Teams)",
        "Browser issues (Chrome, Firefox)",
        "Intranet/SharePoint issues",
        "Antivirus/Security SW issues",
        "Data backup and recovery issues",
        "JDOC",
        "PITECO",
        "DA VINCI",
        "Other"
      ],
      },
      {
        category: "Network",
        subcategories: [
        "Internet connectivity issues",
        "Network drive issues",
        "Wi-Fi connectivity issues",
        "VPN issues",
        "LAN/WAN issues",
        "Firewall/Security issues",
        "Other"
      ],
      },
      {
        category: "Hardware",
        subcategories: [
        "Desktop/Laptop issues",
        "Printer issues",
        "Scanner issues",
        "Peripheral devices (mouse, keyboard…)",
        "Network equipment issues (router, switch…)",
        "Server hardware issues",
        "Workplace moving",
        "Telephone & Mobile issues",
        "Other"
      ],
      },
      {
        category: "SAP",
        subcategories: [
        "FI/CO",
        "MM",
        "SD",
        "WM",
        "SAP FIORI",
        "Other"
      ],
      },
      {
        category: "BI",
        subcategories: [
        "Power BI",
        "BO",
        "Check Corporate",
        "Other"
      ],
      },
      {
        category: "CRM",
        subcategories: [
        "C4C",
        "EMARSYS"
      ],
      },
      {
        category: "DM",
        subcategories: [
        "Hardware",
        "Networking",
        "DM SQL",
        "GEMAR"
      ],
      },
      {
        category: "CRISTINA",
        subcategories: [
        "IT Hardware",
        "IT software (Office, Mail)",
        "Networking",
        "RESCO"
      ],
      },
      {
        category: "DILETTA",
        subcategories: [
        "IT Hardware",
        "IT software (Office, Mail)",
        "Networking",
        "RESCO"
      ],
      },
      {
        category: "SANS SOUCI",
        subcategories: [
        "IT Hardware",
        "IT software (Office, Mail)",
        "Networking",
        "RESCO"
      ],
      },
      {
        category: "HAMBURG",
        subcategories: [
        "IT Hardware",
        "IT software (Office, Mail)",
        "Networking",
        "RESCO"
      ],
      },
    ],
  };

  // Populate Service Level 1 dropdown
  function populateServiceLevel1() {
    serviceLevel1.innerHTML = '<option value="">Select Service Level 1</option>';
    data.ServiceLevel1.forEach((item) => {
      const opt = document.createElement("option");
      opt.value = item.category;
      opt.innerText = item.category;
      serviceLevel1.appendChild(opt);
    });
  }

  // Populate Service Level 2 dropdown based on selected Service Level 1
  function populateServiceLevel2(subcategories) {
    serviceLevel2.innerHTML = '<option value="">Select Service Level 2</option>'; // Clear existing options
    subcategories.forEach((subcategory) => {
      const opt = document.createElement("option");
      opt.value = subcategory;
      opt.innerText = subcategory;
      serviceLevel2.appendChild(opt);
    });
  }

  // Function to check if form is valid
  function checkFormValidity() {
    const isFormValid = serviceLevel1.value && issueDescription.value.trim();
    submitButton.disabled = !isFormValid;

    // Add or remove classes for disabled state
    if (isFormValid) {
      submitButton.classList.remove("bg-gray-400", "cursor-not-allowed");
      submitButton.classList.add("bg-indigo-600", "hover:bg-indigo-700", "focus:ring-indigo-500");
    } else {
      submitButton.classList.add("bg-gray-400", "cursor-not-allowed");
      submitButton.classList.remove("bg-indigo-600", "hover:bg-indigo-700", "focus:ring-indigo-500");
    }
  }

  // Add event listeners to the input fields
  serviceLevel1.addEventListener("change", (e) => {
    const selectedCategory = e.target.value;
    const categoryData = data.ServiceLevel1.find((item) => item.category === selectedCategory);
    if (categoryData) {
      populateServiceLevel2(categoryData.subcategories);
    } else {
      serviceLevel2.innerHTML = '<option value="">Select Service Level 2</option>';
    }
    checkFormValidity();
  });
  issueDescription.addEventListener("input", checkFormValidity);

  // Initial check in case the form is already filled
  checkFormValidity();

  // Populate Service Level 1 on load
  populateServiceLevel1();

  // Optional: Add form submit event to handle form submission
  form.addEventListener("submit", (e) => {
    e.preventDefault();
    if (!submitButton.disabled) {
      // Get values from the form elements
      selectedData = {
        ServiceLevel1: serviceLevel1.value,
        ServiceLevel2: serviceLevel2.value,
        IssueDescription: issueDescription.value.trim(),
      };

      // Log the selected data to the console
      console.log(selectedData);

      // Call the function to handle the data (assuming createAndInsertEmail is defined)
      createAndInsertEmail(selectedData);
    }
  });

  // Helper function to set email subject asynchronously
  function setSubject(subject) {
    return new Promise((resolve, reject) => {
      Office.context.mailbox.item.subject.setAsync(subject, function (result) {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve();
        } else {
          reject(result.error);
        }
      });
    });
  }

  // Helper function to set email recipients asynchronously
  function setRecipients(recipients) {
    return new Promise((resolve, reject) => {
      Office.context.mailbox.item.to.setAsync(recipients, function (result) {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve();
        } else {
          reject(result.error);
        }
      });
    });
  }

  // Helper function to set email body asynchronously
  function setBody(body) {
    return new Promise((resolve, reject) => {
      Office.context.mailbox.item.body.setAsync(body, { coercionType: Office.CoercionType.Html }, function (result) {
        if (result.status === Office.AsyncResultStatus.Succeeded) {
          resolve();
        } else {
          reject(result.error);
        }
      });
    });
  }

  // Function to create and insert the email
  async function createAndInsertEmail(data) {
    const emailBody = `
    <p>${data.IssueDescription}</p>
  `;

    const emailSubject = `Ligabue Group::${selectedData.ServiceLevel1}::${selectedData.ServiceLevel2}`;
    const recipient = "ict.support@ligabue.it";

    try {
      await setSubject(emailSubject);
      console.log("Subject set successfully.");
      await setRecipients([recipient]);
      console.log("Recipient set successfully.");
      await setBody(emailBody);
      console.log("Content set in the mail body successfully.");
    } catch (error) {
      console.error("Error:", error);
    }
  }
});
